@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
:root {
  --bg-color: #0a0a0a;
  --terminal-bg: #121212;
  --text-color: #00ff00;
  --accent-color: #00cc00;
  --secondary-color: #0088ff;
  --warning-color: #ffcc00;
  --error-color: #ff3333;
  --board-color: #1a1a1a;
  --chip-color: #2c2c2c;
  --pin-color: #666;
  --led-off: #333;
  --transition-speed: 0.3s;
  --robot-primary: #0088ff;
  --robot-secondary: #00aaff;
  --robot-accent: #ff3800;

  
  /* Dark theme (default) */
  --bg-color: #1a1a1a;
  --terminal-bg: #2c2c2c;
  --text-color: #f5f5f5;
  --accent-color: #00ff9d;
  --secondary-color: #00cc7e;
  --chip-color: #3a3a3a;

  /* Responsive breakpoints */
  --mobile: 480px;
  --tablet: 768px;
  --desktop: 1024px;

  /* Responsive spacing units */
  --space-unit: 1rem;
  --space-xs: calc(var(--space-unit) * 0.25);
  --space-sm: calc(var(--space-unit) * 0.5);
  --space-md: var(--space-unit);
  --space-lg: calc(var(--space-unit) * 1.5);
  --space-xl: calc(var(--space-unit) * 2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Share Tech Mono', 'Courier New', monospace;
  -webkit-overflow-scrolling: touch;
}

html {
  font-size: 16px;
  -webkit-text-size-adjust: 100%;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
  overflow-x: hidden;
  position: relative;
  padding-left: min(1rem, env(safe-area-inset-left));
  padding-right: min(1rem, env(safe-area-inset-right));
  padding-bottom: min(1rem, env(safe-area-inset-bottom));
}

/* Scene and content containers */
.scene {
  min-height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 1;
  padding: 40px 20px;
}

.content-container {
  max-width: 1200px;
  width: 100%;
  padding: 0 1.5rem;
  position: relative;
  z-index: 2;
}

.title-effect {
  color: var(--secondary-color);
  font-weight: bold;
}

.cta-button {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  background-color: var(--terminal-bg);
  color: var(--text-color);
  text-decoration: none;
  border: 1px solid var(--accent-color);
  font-size: 1rem;
  font-weight: bold;
  margin-right: 1rem;
  margin-top: 1rem;
  transition: all var(--transition-speed) ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  border-radius: 4px;
}

.cta-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 255, 0, 0.2), transparent);
  transition: left 0.5s ease;
}

.cta-button:hover::after {
  left: 100%;
}

.cta-button:hover {
  background-color: rgba(0, 255, 0, 0.1);
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
  transform: translateY(-3px);
}

.section-title {
  font-size: 2.5rem;
  margin-bottom: 3rem;
  position: relative;
  display: inline-block;
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
}

.section-title::before {
  content: '[';
  color: var(--secondary-color);
  margin-right: 10px;
}

.section-title::after {
  content: ']';
  color: var(--secondary-color);
  margin-left: 10px;
}

/* Terminal Component */
.terminal {
  background-color: var(--terminal-bg);
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 4px 20px rgba(0, 255, 0, 0.1);
  position: relative;
  overflow: hidden;
  transition: transform 0.3s, box-shadow 0.3s;
}

.terminal:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 255, 0, 0.2);
}

.terminal::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 24px;
  background: rgba(255, 255, 255, 0.05);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.terminal-header {
  position: absolute;
  top: 5px;
  left: 10px;
  display: flex;
  grid-gap: 6px;
  gap: 6px;
}

.terminal-button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.terminal-button.close {
  background-color: var(--error-color);
}

.terminal-button.minimize {
  background-color: var(--warning-color);
}

.terminal-button.maximize {
  background-color: var(--accent-color);
}

.terminal-content {
  margin-top: 15px;
  padding-top: 10px;
}

.terminal-text {
  overflow: hidden;
  white-space: nowrap;
  margin: 0;
  padding: 0;
  animation: typing 3s steps(60, end);
}

.blinking-cursor {
  display: inline-block;
  width: 10px;
  height: 18px;
  background-color: var(--text-color);
  animation: blink 1s infinite;
  vertical-align: middle;
  margin-left: 4px;
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes blink {
  0%, 49% { opacity: 1; }
  50%, 100% { opacity: 0; }
}

/* Animations */
@keyframes pulse {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 1; }
}

@keyframes speak {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(20%); }
  50% { transform: translateX(-20%); }
  75% { transform: translateX(5%); }
}

@keyframes speakWide {
  0%, 100% { width: 50px; left: 0; }
  50% { width: 30px; left: 10px; }
}

@keyframes robotFloat {
  0%, 100% { transform: translateY(0) rotateY(0); }
  25% { transform: translateY(-10px) rotateY(15deg); }
  50% { transform: translateY(5px) rotateY(0); }
  75% { transform: translateY(-5px) rotateY(-15deg); }
}

@keyframes eyeMove {
  0%, 100% { transform: translate(0, 0); }
  25% { transform: translate(3px, 3px); }
  50% { transform: translate(-3px, 0); }
  75% { transform: translate(0, -3px); }
}

@keyframes navGlow {
  0%, 100% { transform: translateX(-100%); }
  50% { transform: translateX(100%); }
}

@keyframes scrollText {
  0% { transform: translateX(100%); }
  100% { transform: translateX(-100%); }
}

@keyframes leftArmMove {
  0%, 100% { transform: rotate(5deg); }
  20%, 80% { transform: rotate(15deg); }
  40%, 60% { transform: rotate(0deg); }
}

@keyframes rightArmMove {
  0%, 100% { transform: rotate(-5deg); }
  20%, 80% { transform: rotate(-15deg); }
  40%, 60% { transform: rotate(0deg); }
}

@keyframes randomBlink {
  0%, 100% { background: var(--led-off); }
  15% { background: var(--robot-primary); box-shadow: 0 0 5px var(--robot-primary); }
  30% { background: var(--led-off); }
  45% { background: var(--led-off); }
  60% { background: var(--robot-primary); box-shadow: 0 0 5px var(--robot-primary); }
  75% { background: var(--led-off); }
}

@keyframes trackMove {
  0% { left: -20px; opacity: 0.7; }
  100% { left: 100%; opacity: 0; }
}

/* Visibility for animation elements */
.timeline-item, .robot-project, .language-item, .interest-item {
  transform: translateY(50px);
  opacity: 0;
  transition: all 0.6s ease;
}

.timeline-item.visible, 
.robot-project.visible, 
.language-item.visible, 
.interest-item.visible {
  transform: translateY(0);
  opacity: 1;
}

/* Responsive styles */
@media (max-width: 1025px) {
  .theme-toggle {
    top: 20px;
    right: 80px;
  }
}
@media (max-width: 768px) {
  html {
    font-size: 15px;
  }

  h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 2rem;
  }

  h3 {
    font-size: 1.75rem;
  }

  p {
    font-size: 1rem;
  }

  .scene {
    padding: 30px 15px;
  }

  .content-container {
    padding: 0 1rem;
  }

  .timeline::after {
    left: 31px;
  }
  
  .timeline-item {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  
  .timeline-item.right {
    left: 0;
  }
  
  .timeline-item.left::after,
  .timeline-item.right::after {
    left: 21px;
  }
  
  .hero-content h1 {
    font-size: 2.5rem;
  }
  
  .section-title {
    font-size: 2rem;
    margin-bottom: 2rem;
  }
  
  .robot-diagram-container {
    height: 400px;
  }
  
  .robot-model-container {
    min-height: 300px;
  }
  
  .robot-model {
    width: 220px;
    height: 280px;
  }

  button,
  .button,
  a {
    min-height: 44px;
    min-width: 44px;
  }

  input,
  select,
  textarea {
    font-size: 16px; /* Prevents zoom on focus in iOS */
  }

  ::-webkit-scrollbar {
    width: 0px;
    height: 0px;
  }

  * {
    scrollbar-width: none;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 14px;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.75rem;
  }

  h3 {
    font-size: 1.5rem;
  }

  p {
    font-size: 0.95rem;
  }

  .scene {
    padding: 20px 10px;
  }

  .content-container {
    padding: 0 0.8rem;
  }

  .hero-content h1 {
    font-size: 2rem;
  }

  .section-title {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
  }

  .cta-button {
    padding: 0.6rem 1.2rem;
    font-size: 0.9rem;
  }

  .cta-button {
    width: 100%;
    margin-right: 0;
    margin-bottom: 1rem;
    text-align: center;
  }

  .robot-diagram-container {
    height: 350px;
  }
  
  .robot-part {
    font-size: 0.7rem;
  }
  
  .robot-part.cpu {
    width: 90px;
    height: 90px;
  }
  
  .robot-part.sensors,
  .robot-part.communication,
  .robot-part.motor-controller,
  .robot-part.power {
    width: 80px;
    height: 50px;
  }
}

.profile-picture-container {
  width: 300px;
  height: 300px;
  position: relative;
  transition: transform 0.3s ease-out;
  transform-style: preserve-3d;
}

.profile-frame {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 20px;
  background: var(--terminal-bg);
  border-radius: 10px;
  overflow: hidden;
}

.profile-image {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 5px;
}

.profile-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: contrast(110%) brightness(110%);
}

.frame-corner {
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid var(--robot-primary);
  z-index: 2;
}

.top-left {
  top: 0;
  left: 0;
  border-right: none;
  border-bottom: none;
}

.top-right {
  top: 0;
  right: 0;
  border-left: none;
  border-bottom: none;
}

.bottom-left {
  bottom: 0;
  left: 0;
  border-right: none;
  border-top: none;
}

.bottom-right {
  bottom: 0;
  right: 0;
  border-left: none;
  border-top: none;
}

.frame-edge {
  position: absolute;
  background: var(--robot-primary);
  opacity: 0.5;
  z-index: 2;
}

.top, .bottom {
  height: 2px;
  width: calc(100% - 40px);
  left: 20px;
}

.left, .right {
  width: 2px;
  height: calc(100% - 40px);
  top: 20px;
}

.top { top: 0; }
.right { right: 0; }
.bottom { bottom: 0; }
.left { left: 0; }

.scanning-line {
  position: absolute;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--robot-primary) 50%, 
    transparent 100%
  );
  top: 0;
  left: 0;
  z-index: 3;
  animation: scan 3s linear infinite;
  opacity: 0.7;
  box-shadow: 0 0 10px var(--robot-primary);
}

.frame-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    transparent 0%,
    rgba(0, 136, 255, 0.1) 50%,
    transparent 100%
  );
  pointer-events: none;
  z-index: 2;
}

@keyframes scan {
  0% { transform: translateY(0); }
  100% { transform: translateY(300px); }
}

/* Glitch effect on hover */
.profile-image:hover img {
  animation: glitch 0.5s infinite;
}

@keyframes glitch {
  0% { transform: translate(0); }
  20% { transform: translate(-2px, 2px); }
  40% { transform: translate(-2px, -2px); }
  60% { transform: translate(2px, 2px); }
  80% { transform: translate(2px, -2px); }
  100% { transform: translate(0); }
}

.profile-container {
  width: 300px;
  height: 300px;
  position: relative;
  perspective: 1000px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .profile-container {
    width: 250px;
    height: 250px;
  }
}

@media (max-width: 480px) {
  .profile-container {
    width: 200px;
    height: 200px;
  }
  
  .frame {
    border-width: 8px;
  }
  
  .frame::before,
  .frame::after {
    border-width: 8px;
  }
}
#hero {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
  padding: 2rem 0;
}

.hero-content {
  text-align: center;
  transition: transform 0.5s;
  z-index: 2;
  width: 100%;
}

.hero-flex {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  max-width: 1200px;
  flex-wrap: wrap;
  grid-gap: 2rem;
  gap: 2rem;
  padding: 0 1rem;
  align-items: center;
}

.hero-text {
  flex: 1 1;
  min-width: 300px;
  text-align: left;
  padding: 2rem;
}

.hero-profile {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 300px;
  perspective: 1000px;
}

.cta-buttons {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-top: 2rem;
}

.hero-content h1 {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
  text-shadow: 0 0 15px rgba(0, 255, 0, 0.7);
  transition: transform 0.5s;
  cursor: pointer;
}

.hero-content h1:hover {
  transform: scale(1.05);
  text-shadow: 0 0 25px rgba(0, 255, 0, 0.9);
}

.hero-subtitle {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  opacity: 0.8;
}

/* 3D Robot Model */
.robot-model-container {
  flex: 1 1;
  min-width: 300px;
  min-height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  perspective: 1000px;
}

.robot-model {
  width: 280px;
  height: 350px;
  position: relative;
  transform-style: preserve-3d;
  animation: robotFloat 5s ease-in-out infinite;
}

.robot-model-part {
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 0.5s;
}

.robot-model-head {
  width: 120px;
  height: 100px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 20px;
  top: 30px;
  left: 80px;
  z-index: 3;
  transform-style: preserve-3d;
  box-shadow: 0 0 20px rgba(0, 136, 255, 0.5);
}

.robot-model-eye {
  position: absolute;
  width: 25px;
  height: 25px;
  background: #000;
  border-radius: 50%;
  top: 30px;
  border: 2px solid var(--robot-primary);
  overflow: hidden;
  z-index: 4;
}

.robot-model-eye.left {
  left: 25px;
}

.robot-model-eye.right {
  right: 25px;
}

.robot-model-eye-light {
  position: absolute;
  width: 10px;
  height: 10px;
  background: var(--robot-primary);
  border-radius: 50%;
  top: 7px;
  left: 7px;
  box-shadow: 0 0 10px var(--robot-primary);
  animation: eyeMove 3s infinite alternate;
}

.robot-model-mouth {
  position: absolute;
  width: 50px;
  height: 10px;
  background: #000;
  bottom: 20px;
  left: 35px;
  border-radius: 5px;
  overflow: hidden;
}

.robot-model-mouth-line {
  position: absolute;
  height: 2px;
  width: 50px;
  background: var(--robot-primary);
  top: 4px;
  animation: speakWide 2s infinite;
}

.robot-model-body {
  width: 180px;
  height: 150px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 20px;
  top: 120px;
  left: 50px;
  z-index: 2;
  box-shadow: 0 0 20px rgba(0, 136, 255, 0.3);
}

.robot-model-chest-light {
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #000;
  top: 30px;
  left: 65px;
  overflow: hidden;
  border: 2px solid var(--robot-primary);
}

.robot-model-light-effect {
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, var(--robot-primary) 0%, transparent 70%);
  opacity: 0.7;
  animation: pulse 2s infinite;
}

.robot-model-panel {
  position: absolute;
  bottom: 20px;
  width: 120px;
  height: 30px;
  background: #000;
  left: 30px;
  border-radius: 5px;
  border: 1px solid var(--robot-primary);
}

.robot-model-button-row {
  display: flex;
  justify-content: space-around;
  margin-top: 5px;
}

.robot-model-button {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: var(--led-off);
  animation: randomBlink 5s infinite;
}

.robot-model-button:nth-child(2) {
  animation-delay: 0.7s;
}

.robot-model-button:nth-child(3) {
  animation-delay: 1.3s;
}

.robot-model-button:nth-child(4) {
  animation-delay: 1.9s;
}

.robot-model-button:nth-child(5) {
  animation-delay: 2.5s;
}

.robot-model-arm {
  width: 30px;
  height: 120px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 15px;
  position: absolute;
  top: 130px;
  z-index: 1;
}

.robot-model-arm.left {
  left: 10px;
  transform-origin: top center;
  animation: leftArmMove 10s infinite;
}

.robot-model-arm.right {
  right: 10px;
  transform-origin: top center;
  animation: rightArmMove 10s infinite;
}

.robot-model-hand {
  position: absolute;
  width: 40px;
  height: 40px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 10px;
  bottom: -20px;
  left: -5px;
}

.robot-model-finger {
  position: absolute;
  width: 8px;
  height: 15px;
  background: var(--robot-primary);
  border-radius: 4px;
  bottom: -5px;
}

.finger-1 { left: 5px; }
.finger-2 { left: 16px; }
.finger-3 { left: 27px; }

.robot-model-leg {
  width: 40px;
  height: 100px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  border-radius: 15px;
  position: absolute;
  top: 260px;
  z-index: 1;
}

.robot-model-leg.left {
  left: 60px;
}

.robot-model-leg.right {
  right: 60px;
}

.robot-model-foot {
  position: absolute;
  width: 50px;
  height: 20px;
  background: var(--robot-primary);
  bottom: -10px;
  left: -5px;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0, 136, 255, 0.5);
}

@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 3rem;
  }
  
  .robot-model-container {
    min-height: 300px;
  }
  
  .robot-model {
    width: 220px;
    height: 280px;
  }

  .hero-flex {
    flex-direction: column-reverse;
    text-align: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
  
  .hero-text {
    margin-top: 2rem;
    text-align: center;
    padding: 0;
  }
  
  .cta-buttons {
    justify-content: center;
  }

  .hero-subtitle {
    font-size: 1.2rem;
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
    font-size: 2.2rem;
  }

  #hero {
    padding: 1rem 0;
  }

  .hero-subtitle {
    font-size: 1rem;
  }

  .cta-buttons {
    flex-direction: column;
    width: 100%;
    grid-gap: 0.8rem;
    gap: 0.8rem;
  }

  .cta-button {
    width: 100%;
    text-align: center;
  }
}
/* About component specific styles */
#about {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-section {
  margin: 2rem 0;
  padding: 1rem;
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 3rem;
  gap: 3rem;
  align-items: center;
}

.about-text {
  line-height: 1.8;
  color: var(--text-color);
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  margin-top: 2rem;
}

.tech-item {
  background: var(--terminal-bg);
  padding: 0.6rem 1.2rem;
  border-radius: 4px;
  border: 1px solid var(--robot-primary);
  color: var(--robot-primary);
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.tech-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0, 136, 255, 0.3);
  background: var(--chip-color);
}

@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }

  .about-text {
    text-align: center;
    padding: 0 1rem;
  }

  .tech-stack {
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .tech-item {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }
}

@media (max-width: 480px) {
  .about-text {
    font-size: 0.9rem;
    padding: 0 0.5rem;
  }

  .tech-stack {
    flex-wrap: wrap;
    grid-gap: 0.8rem;
    gap: 0.8rem;
  }

  .tech-item {
    font-size: 0.8rem;
    padding: 0.4rem 0.8rem;
  }
}

.circuit-board {
  background-color: var(--board-color);
  border-radius: 10px;
  padding: 30px;
  margin-bottom: 40px;
  position: relative;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  border: 1px solid var(--accent-color);
  overflow: hidden;
  transition: transform 0.5s;
  transform-style: preserve-3d;
}

.circuit-board:hover {
  transform: translateY(-10px) rotateX(5deg);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.7);
}

.circuit-board::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
      linear-gradient(90deg, transparent 99%, var(--accent-color) 99.5%, transparent 100%),
      linear-gradient(0deg, transparent 99%, var(--accent-color) 99.5%, transparent 100%);
  background-size: 20px 20px;
  opacity: 0.1;
  pointer-events: none;
}

.chip {
  background-color: var(--chip-color);
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 20px;
  position: relative;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.chip:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.5);
}

.chip::before {
  content: '';
  position: absolute;
  top: -5px;
  left: 10px;
  width: 30px;
  height: 5px;
  background-color: var(--pin-color);
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}

.chip::after {
  content: '';
  position: absolute;
  bottom: -5px;
  right: 10px;
  width: 30px;
  height: 5px;
  background-color: var(--pin-color);
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.chip-title {
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: var(--secondary-color);
}

.pin-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.pin {
  width: 10px;
  height: 10px;
  background-color: var(--pin-color);
  border-radius: 50%;
}

/* Skill meters */
.skill-meter {
  width: 100%;
  height: 20px;
  background-color: var(--terminal-bg);
  border-radius: 10px;
  overflow: hidden;
  margin-top: 10px;
  position: relative;
  border: 1px solid var(--accent-color);
  margin-bottom: 15px;
}

.skill-progress {
  height: 100%;
  background: linear-gradient(90deg, var(--accent-color), var(--secondary-color));
  border-radius: 10px;
  width: 0; /* Will be set dynamically with JS */
  transition: width 1.5s ease;
}

.skill-dots {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  align-items: center;
}

.skill-dot {
  width: 3px;
  height: 10px;
  background-color: rgba(0, 0, 0, 0.3);
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
  margin: 2rem 0;
}

@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }

  .skill-category {
    padding: 1.2rem;
  }

  .skill-title {
    font-size: 1.1rem;
  }

  .skill-list {
    grid-gap: 0.8rem;
    gap: 0.8rem;
  }

  .skill-item {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }
}

@media (max-width: 480px) {
  .skills-grid {
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .skill-category {
    padding: 1rem;
  }

  .skill-title {
    font-size: 1rem;
  }

  .skill-list {
    grid-gap: 0.6rem;
    gap: 0.6rem;
  }

  .skill-item {
    font-size: 0.85rem;
    padding: 0.4rem 0.8rem;
  }
}
.timeline {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 2px;
  background-color: var(--robot-primary);
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -1px;
  box-shadow: 0 0 10px var(--robot-primary);
}

.timeline-item {
  position: relative;
  width: 50%;
  padding: 1rem 2rem;
  box-sizing: border-box;
  margin-bottom: 2rem;
  transform: translateY(50px);
  opacity: 0;
  transition: all 0.8s ease;
  cursor: pointer;
}

.timeline-item.visible {
  transform: translateY(0);
  opacity: 1;
}

.timeline-item:hover {
  transform: scale(1.05);
  z-index: 10;
}

.timeline-item.left {
  left: 0;
}

.timeline-item.right {
  left: 50%;
}

.timeline-item::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: var(--terminal-bg);
  border: 3px solid var(--robot-primary);
  top: 1rem;
  z-index: 1;
  box-shadow: 0 0 10px var(--robot-primary);
}

.timeline-item.left::after {
  right: -13px;
}

.timeline-item.right::after {
  left: -13px;
}

.timeline-content {
  padding: 1.5rem;
  background-color: var(--terminal-bg);
  border-radius: 6px;
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.1);
  position: relative;
  border: 1px solid var(--robot-primary);
  transition: transform 0.3s, box-shadow 0.3s;
}

.timeline-content:hover {
  box-shadow: 0 0 25px rgba(0, 136, 255, 0.3);
}

.timeline-date {
  display: inline-block;
  padding: 0.3rem 0.8rem;
  background-color: var(--chip-color);
  color: var(--robot-primary);
  font-size: 0.9rem;
  border-radius: 4px;
  font-weight: bold;
  margin-bottom: 0.5rem;
  border: 1px solid var(--robot-primary);
}

.timeline-title {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  color: var(--robot-primary);
}

@media (max-width: 768px) {
  .timeline {
    margin: 20px auto;
    padding: 20px;
  }

  .timeline::after {
    left: 31px;
  }
  
  .timeline-item {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

  .timeline-item::before {
    left: 60px;
    border: medium solid var(--accent-color);
    border-width: 10px 10px 10px 0;
    border-color: transparent var(--accent-color) transparent transparent;
  }
  
  .timeline-item.left::after,
  .timeline-item.right::after {
    left: 22px;
  }
  
  .timeline-item.right {
    left: 0%;
  }
  
  .timeline-item.left {
    left: 0%;
  }

  .timeline-content {
    padding: 15px;
  }

  .timeline-date {
    font-size: 0.9rem;
  }

  .timeline-title {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .timeline {
    margin: 15px auto;
    padding: 15px;
  }

  .timeline-content {
    padding: 12px;
  }

  .timeline-date {
    font-size: 0.8rem;
  }

  .timeline-title {
    font-size: 1rem;
  }

  .timeline-description {
    font-size: 0.9rem;
  }
}
