.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.modal-backdrop.visible {
  opacity: 1;
}

.modal-content {
  background: var(--terminal-bg);
  border: 2px solid var(--robot-primary);
  border-radius: 10px;
  padding: 2rem;
  width: 90%;
  max-width: 1200px; /* Added max-width */
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  transform: translateY(20px);
  opacity: 0;
  transition: all 0.3s ease;
}

.modal-backdrop.visible .modal-content {
  transform: translateY(0);
  opacity: 1;
}

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  color: var(--robot-primary);
  font-size: 2rem;
  cursor: pointer;
  padding: 0.5rem;
  line-height: 1;
}

.modal-header {
  margin-bottom: 1.5rem;
}

.modal-title {
  color: var(--robot-primary);
  margin: 0;
  font-size: 1.8rem;
}

.modal-body {
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.project-visual {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  overflow: hidden;
  background: var(--terminal-bg);
}

.project-visual .carousel-container {
  width: 100%;
  max-height: 70vh;
  aspect-ratio: 16/9;
}

.modal-description {
  color: var(--text-color);
  line-height: 1.6;
  margin: 0;
}

.modal-tech {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 0.8rem;
  gap: 0.8rem;
}

.modal-tech-tag {
  background: var(--chip-color);
  color: var(--robot-primary);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  border: 1px solid var(--robot-primary);
}

@media (max-width: 768px) {
  .modal-content {
    padding: 1rem;
    width: 95%;
  }

  .project-visual .carousel-container {
    aspect-ratio: auto;
    height: 300px;
  }

  .modal-title {
    font-size: 1.5rem;
  }

  .modal-tech-tag {
    font-size: 0.8rem;
    padding: 0.4rem 0.8rem;
  }
}
.carousel-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 8px;
  background: var(--terminal-bg);
}

.carousel-image {
  width: 100%;
  height: 100%;
  object-fit: contain; /* Changed from cover to contain */
  background: var(--terminal-bg);
  padding: 0.5rem;
}

/* Ensure modal view has different sizing */
.modal-body .carousel-container {
  min-height: 400px;
  max-height: 70vh;
}

.modal-body .carousel-image {
  max-height: 70vh;
  padding: 1rem;
}

.carousel-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 2rem;
  line-height: 1;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease;
}

.carousel-button:hover {
  background: rgba(0, 0, 0, 0.8);
}

.carousel-button.prev {
  left: 1rem;
}

.carousel-button.next {
  right: 1rem;
}

.carousel-dots {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.carousel-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  border: none;
  padding: 0;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.carousel-dot.active {
  background: white;
}

