.robot-controls {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.robot-button {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: var(--led-off);
  cursor: pointer;
  border: 1px solid var(--robot-primary);
  transition: all 0.3s;
}

.robot-button:hover, .robot-button.active {
  background: var(--robot-primary);
  box-shadow: 0 0 10px var(--robot-primary);
}

.robot-arm {
  position: absolute;
  width: 15px;
  height: 70px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  bottom: 90px;
  z-index: 0;
  transition: all 0.5s;
}

.robot-arm.left {
  left: 30px;
  transform-origin: bottom center;
  transform: rotate(15deg);
  border-radius: 5px 5px 0 0;
}

.robot-arm.right {
  right: 30px;
  transform-origin: bottom center;
  transform: rotate(-15deg);
  border-radius: 5px 5px 0 0;
}

.robot-hand {
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 5px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  display: flex;
  justify-content: space-around;
  align-items: center;
  top: -12px;
}

.robot-finger {
  width: 4px;
  height: 12px;
  background: var(--robot-primary);
  border-radius: 2px;
}

.robot-leg {
  position: absolute;
  width: 20px;
  height: 50px;
  background: var(--chip-color);
  border: 2px solid var(--robot-primary);
  bottom: 0;
  z-index: 0;
  border-radius: 0 0 5px 5px;
}

.robot-leg.left {
  left: 40px;
}

.robot-leg.right {
  right: 40px;
}

.robot-wheel {
  position: absolute;
  width: 30px;
  height: 15px;
  background: var(--robot-primary);
  border-radius: 7px;
  bottom: -7px;
  left: 50%;
  transform: translateX(-50%);
}

.robot-jetpack {
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 20px;
  opacity: 0;
  transition: opacity 0.3s;
}

.robot-jetpack:before,
.robot-jetpack:after {
  content: '';
  position: absolute;
  width: 15px;
  height: 25px;
  background: var(--robot-primary);
  border-radius: 0 0 10px 10px;
}

.robot-jetpack:before { left: 0; }
.robot-jetpack:after { right: 0; }

#robot-assistant.moving .robot-jetpack {
  opacity: 1;
}

#robot-assistant.moving .robot-jetpack:before,
#robot-assistant.moving .robot-jetpack:after {
  animation: jetpackEffect 0.2s infinite;
}

.robot-trail {
  position: absolute;
  width: 2px;
  height: 20px;
  background: linear-gradient(to bottom, var(--robot-primary), transparent);
  opacity: 0;
  transition: opacity 0.3s;
}

#robot-assistant.moving .robot-trail {
  opacity: 0.5;
  animation: trailEffect 1s infinite;
}

@keyframes moveShake {
  0%, 100% { 
    margin-top: 0px;
    transform: translate(var(--x), var(--y)) rotate(0deg);
  }
  25% { 
    margin-top: -5px;
    transform: translate(var(--x), var(--y)) rotate(-2deg);
  }
  75% { 
    margin-top: -5px;
    transform: translate(var(--x), var(--y)) rotate(2deg);
  }
}

@keyframes jetpackEffect {
  0%, 100% { opacity: 0.4; transform: scaleY(1); }
  50% { opacity: 0.8; transform: scaleY(1.2); }
}

@keyframes trailEffect {
  0% { height: 20px; opacity: 0.5; }
  100% { height: 5px; opacity: 0; }
}

@keyframes textGlow {
  0%, 100% { text-shadow: 0 0 5px var(--robot-primary); }
  50% { text-shadow: 0 0 15px var(--robot-primary); }
}

/* Robot animation states */
#robot-assistant:hover .robot-arm.left {
  transform: rotate(45deg);
}

#robot-assistant:hover .robot-arm.right {
  transform: rotate(-45deg);
}

#robot-assistant:hover .robot-pupil {
  background: var(--robot-accent);
}

.robot-dialog {
  position: absolute;
  top: -80px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--terminal-bg);
  border: 2px solid var(--robot-primary);
  border-radius: 10px;
  padding: 15px;
  width: 200px;
  box-shadow: 0 0 20px rgba(0, 136, 255, 0.3);
  opacity: 0;
  animation: dialogAppear 0.3s forwards;
  max-width: calc(100vw - 40px);
  z-index: 1001;
}

.dialog-options {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

.dialog-options button {
  background: var(--chip-color);
  border: 1px solid var(--robot-primary);
  color: var(--robot-primary);
  padding: 8px 12px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
  font-family: 'Share Tech Mono', monospace;
}

.dialog-options button:hover {
  background: var(--robot-primary);
  color: var(--terminal-bg);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 136, 255, 0.3);
}

@keyframes dialogAppear {
  from {
    opacity: 0;
    transform: translate(-50%, 10px);
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

#robot-assistant.moving .robot-head {
  animation: moveHead 0.5s infinite;
}

@keyframes moveHead {
  0%, 100% { transform: translateX(-50%) rotate(0deg); }
  25% { transform: translateX(-50%) rotate(-5deg); }
  75% { transform: translateX(-50%) rotate(5deg); }
}

#robot-assistant.moving .robot-arm.left {
  animation: moveLeftArm 0.5s infinite;
}

#robot-assistant.moving .robot-arm.right {
  animation: moveRightArm 0.5s infinite;
}

@keyframes moveLeftArm {
  0%, 100% { transform: rotate(15deg); }
  50% { transform: rotate(45deg); }
}

@keyframes moveRightArm {
  0%, 100% { transform: rotate(-15deg); }
  50% { transform: rotate(-45deg); }
}

@media (max-width: 480px) {
  .robot-message-container {
    min-width: 200px;
    max-width: calc(100vw - 40px);
    left: 50% !important;
    margin: 0 10px;
  }
}

@media (max-width: 768px) {
  #robot-assistant {
    transform: scale(0.7);
    margin-top: 0;
    z-index: 1100;
  }

  .robot-message-container {
    min-width: 220px;
    max-width: 280px;
    transform: translate(-50%, -120%) scale(0.9);
  }

  .robot-message-container.visible {
    transform: translate(-50%, -100%) scale(0.9);
  }
}

@media (max-width: 480px) {
  #robot-assistant {
    transform: scale(0.6);
    margin-top: 0;
    right: -20px;
  }

  .robot-message-container {
    min-width: 200px;
    max-width: 260px;
    font-size: 0.9rem;
    transform: translate(-50%, -120%) scale(0.8);
  }

  .robot-message-container.visible {
    transform: translate(-50%, -100%) scale(0.8);
  }

  .robot-dialog {
    transform: scale(0.8) translateX(-60%);
    width: 180px;
  }
}

footer {
  text-align: center;
  padding: 2rem 0;
  opacity: 0.7;
  font-size: 0.9rem;
  margin-top: 3rem;
  border-top: 1px solid rgba(0, 136, 255, 0.2);
  position: relative;
}

.robot-tracks {
  position: absolute;
  bottom: 90%;
  left: 0;
  width: 100%;
  height: 20px;
  overflow: hidden;
}

.track {
  position: absolute;
  width: 15px;
  height: 5px;
  background: var(--robot-primary);
  opacity: 0.3;
  border-radius: 2px;
  animation: trackMove 10s linear infinite;
}

.track:nth-child(2) {
  animation-delay: 1s;
  top: 8px;
}

.track:nth-child(3) {
  animation-delay: 2s;
}

.track:nth-child(4) {
  animation-delay: 3s;
  top: 8px;
}

.track:nth-child(5) {
  animation-delay: 4s;
}

.track:nth-child(6) {
  animation-delay: 5s;
  top: 8px;
}

@media (max-width: 768px) {
  footer {
    padding: 2rem 1rem;
  }

  .footer-content {
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    text-align: center;
  }

  .footer-links {
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .footer-social {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  footer {
    padding: 1.5rem 0.8rem;
  }

  .footer-content {
    grid-gap: 1.2rem;
    gap: 1.2rem;
  }

  .footer-links {
    flex-wrap: wrap;
    grid-gap: 0.8rem;
    gap: 0.8rem;
  }

  .footer-social-link {
    width: 35px;
    height: 35px;
    font-size: 1.1rem;
  }
}
.circuit-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none;
  opacity: 0.15;
}

.circuit-line {
  position: absolute;
  background: var(--robot-primary);
  opacity: 0.3;
}

.circuit-node {
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: var(--accent-color);
  opacity: 0.5;
}

.circuit-dot {
  position: absolute;
  width: 4px;
  height: 4px;
  background: var(--robot-primary);
  border-radius: 50%;
}

@media (max-width: 768px) {
  .circuit-background {
    opacity: 0.1;
  }

  .circuit-line {
    opacity: 0.2;
  }

  .circuit-dot {
    width: 3px;
    height: 3px;
  }
}

@media (max-width: 480px) {
  .circuit-background {
    opacity: 0.08;
  }
  
  /* Reduce animation complexity on mobile */
  .circuit-line, .circuit-dot {
    animation: none !important;
  }
  
  /* Reduce the number of elements rendered on mobile */
  .circuit-line:nth-child(odd) {
    display: none;
  }
}

.custom-cursor {
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.2);
  border: 2px solid #ffffff;
  border-radius: 50%;
  position: fixed;
  pointer-events: none;
  z-index: 9999;
  transition: width 0.2s ease-out, height 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out;
  transform: translate(-50%, -50%);
  will-change: transform;
}

.custom-cursor.hidden {
  opacity: 0;
}

.custom-cursor.enlarged {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.3);
  border-color: #ffffff;
  mix-blend-mode: difference;
}

.custom-cursor.dragging {
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.4);
  border-color: #4A90E2;
  mix-blend-mode: normal;
}

.cursor-dot {
  width: 4px;
  height: 4px;
  background: var(--robot-primary);
  border-radius: 50%;
  position: fixed;
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
}

/* Hide custom cursor on touch devices */
@media (hover: none) and (pointer: coarse) {
  .custom-cursor,
  .cursor-dot {
    display: none;
  }
}

/* When hovering over robot */
#robot-assistant:hover ~ .custom-cursor {
  width: 35px;
  height: 35px;
  border-color: #4A90E2;
  background: rgba(74, 144, 226, 0.2);
}

/* When actively dragging */
#robot-assistant:active ~ .custom-cursor {
  width: 45px;
  height: 45px;
  background: rgba(74, 144, 226, 0.3);
}

@media (hover: none) {
  .custom-cursor {
    display: none;
  }
}

.language-switcher {
  position: fixed;
  top: 4rem;
  left: 2rem;
  z-index: 100;
  background-color: var(--terminal-bg);
  border: 1px solid var(--accent-color);
  color: var(--text-color);
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 4px;
  transition: all 0.3s ease;
  box-shadow: 0 2px 10px rgba(0, 255, 0, 0.2);
}

.language-switcher:hover {
  background-color: var(--accent-color);
  color: var(--terminal-bg);
  transform: translateY(-2px);
}

.language-switcher:active {
  transform: translateY(0);
}

@media (max-width: 768px) {
  .language-switcher {
    top: 2rem;
    left: 1rem;
    padding: 0.4rem 0.8rem;
    font-size: 0.9rem;
  }
}

.App {
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
}

main {
  position: relative;
  z-index: 1;
  padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);
}

@media (max-width: 768px) {
  main {
    padding: 0 1rem;
  }
}

@media (max-width: 480px) {
  main {
    padding: 0 0.8rem;
  }
}

